import React, { useState } from "react";
import {
  Box,
  Button,
  makeStyles,
  Snackbar,
  Typography,
} from "@material-ui/core";
import googleAuthenticator from "../../../../assets/images/2fa.png";
import { useDispatch, useSelector } from "react-redux";
import { useHistory } from "react-router";
import DialogDisable from "./DialogDisable";
import { Alert } from "@mui/material";
import { setData } from "../../../../redux/reducers/2fa";
import EditIcon from "@mui/icons-material/Edit";

const Security = ({ t }) => {
  const classes = useStyles();
  const history = useHistory();
  const dispatch = useDispatch();

  const [open, setOpen] = useState(false);

  const { securityParameters } = useSelector(
    ({ MyProfileReducer }) => MyProfileReducer
  );

  const { response } = useSelector(({ f2aReducer }) => f2aReducer);

  return (
    <Box className={classes.wrapper}>
      <Snackbar
        open={response?.open}
        autoHideDuration={5000}
        onClose={() => dispatch(setData(null))}
        anchorOrigin={{ vertical: "top", horizontal: "center" }}
      >
        <Alert
          severity={response?.severity === 200 ? "success" : "error"}
          color={response?.severity === 200 ? "success" : "error"}
          variant="filled"
        >
          {response?.message?.messages
            ? response?.message?.messages
            : t("successDisable")}
        </Alert>
      </Snackbar>

      <div
        style={{
          borderBottom: "1px solid #E7EAEE",
          paddingBottom: 24,
          marginBottom: 24,
        }}
      >
        <Typography variant="h5" className={classes.title}>
          <img src={googleAuthenticator} className={classes.img} alt="" />
          Google Authenticator
        </Typography>
        <Typography
          variant="h6"
          style={{ fontWeight: "400", color: "#64748B", lineHeight: 1 }}
        >
          Используется для ввода кода подтверждения Google при снятии и
          изменении настроек безопасности
        </Typography>
      </div>

      <Box display="flex">
        <Button
          variant="contained"
          size="large"
          color="primary"
          style={{
            backgroundColor:
              securityParameters?.data?.security &&
              securityParameters?.data?.security["2fa"]
                ? "#d32f2f"
                : "#66bb6a",
            color: "#fff",
            marginRight: 16,
          }}
          onClick={() => {
            securityParameters?.data?.security &&
            securityParameters?.data?.security["2fa"]
              ? setOpen(true)
              : history.push("/dashboard/settings/security/2fa");
          }}
        >
          {securityParameters?.data?.security &&
          securityParameters?.data?.security["2fa"]
            ? t("disable")
            : t("enable")}
        </Button>
        {securityParameters?.data?.security &&
          securityParameters?.data?.security["2fa"] && (
            <Button
              size="large"
              color="secondary"
              variant="outlined"
              onClick={() =>
                history.push("/dashboard/settings/security/2fa/edit")
              }
            >
              <EditIcon />
            </Button>
          )}
      </Box>

      <DialogDisable setOpen={setOpen} open={open} t={t} />
    </Box>
  );
};

export default Security;

const useStyles = makeStyles((theme) => ({
  wrapper: {
    background: "#FFFFFF",
    boxShadow: "0px 1px 0px #E2E8F0",
    borderRadius: 8,
    padding: 32,
    color: "#000",
    [theme.breakpoints.down("sm")]: {
      padding: 16,
    },
  },
  title: {
    fontWeight: "600",
    display: "flex",
    alignItems: "center",
    marginBottom: 16,
  },
  img: {
    width: 40,
    height: 40,
    marginRight: 16,
  },
}));
